<template>
    <div id="dashboard">
        <h3>Dashboard</h3>

        <!-- Data display -->
        <ul class="collection with-header">
            <li class="collection-header"><h4>Employees</h4></li>

            <!-- Run through the employees array -->
            <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item">
                
                <!-- Harvesting Employee details -->
                <div class="chip">{{employee.department}}</div>
                {{employee.employee_id}}:{{employee.name}}
                
                <!-- Link to employee page -->
                <router-link class="secondary-content" v-bind:to="{name:'view-employee', params: {employee_id: employee.employee_id}}">
                    <i class="fa fa-eye"></i>
                </router-link>
            
            </li>
        </ul>

        <!-- Big Red Button -->
        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>

    </div>
</template>

<script>
import db from './firebaseInit'

export default {
    name: 'dashboard',
    data () {
        return {
            // creating employees array object
            employees: []
        }
    },
    created () {
        // run through the firestore and collect the docs
        db.collection('employees').orderBy('department').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'employee_id': doc.data().employee_id,
                    'name' : doc.data().name,
                    'department' : doc.data().department,
                    'position' : doc.data().position
                }

                // Push data to employees array
                this.employees.push(data)
            })
        })
    }
}
</script>